:host {
  display: block;
  /* padding: 50px 0px; */
  height: 50px;
}

header {
  background: #0d3080;
  color: white;
  height: 50px;
  display: flex;
  align-items: center;
  box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.26);
  justify-content: space-between;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 100;
}

@media (max-width: 349px) {
  :host {
    padding: 75px 0px 50px 0px;
    height: calc(100vh);
  }
  
  header {
    height: 75px;
  }
} 