:host {
  display: flex;
  flex-flow: column;
  height: 100%;
}

.background-container {
  /* background-image: url('./../../../assets/image/homepage.jpg'); */
  background-image: url('https://greenstays.pt/wp-content/uploads/2020/06/1065177.jpg');
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  height: calc(100vh - 50px - 50px);
  top: 50px;
}

.background-container > h2 {
  margin: 28px;
  color: white;
}
